.rrbe-map{position:relative;width:100%;height:400px}.rrbe-map__preloader{position:absolute;height:100%;top:0;left:0;right:0;bottom:0;background-color:rgba(255,255,255,0.6);z-index:9999;text-align:center;display:flex;justify-content:center;align-items:center}.rrbe-map__preloader img{display:inline-block;width:64px;height:64px}.rrbe-map__container{position:absolute;top:0;right:0;left:0;bottom:0}.rrbe-map__paint{background-color:#fff;box-shadow:0 1px 5px rgba(0,0,0,0.65);border-radius:4px;text-align:center}.rrbe-map__paint button{margin-top:3px;background:none;border:0;cursor:pointer}.rrbe-map__modal{font-family:Arial, Helvetica, sans-serif}.rrbe-map__modal .modal__body{position:absolute;display:none;width:25%;min-height:300px;max-height:50vh;overflow:auto;left:50px;top:10px;background-color:#fff;z-index:9999;box-shadow:0 0 20px 20px rgba(0,0,0,0.1);border-radius:4px;padding:20px}.rrbe-map__modal .modal__body.open{display:block}.rrbe-map__modal .modal__body .close{position:absolute;top:10px;right:10px;padding:5px 7px;border:0}.rrbe-map__modal .modal__body-color label{color:#919191;display:flex;flex-direction:column;font-size:0.9rem}.rrbe-map__modal .modal__body-color button{border:0;width:30px;height:30px;background:none;margin:10px 10px 20px 0;cursor:pointer}.rrbe-map__modal .modal__body-color button.active{background-image:url(/modules/editor/resources/modules/editor/src/js/components/altrp-map/assets/correct.png);background-position:center center;background-repeat:no-repeat}.rrbe-map__modal .modal__body-text label{color:#919191;display:flex;flex-direction:column;font-size:0.9rem}.rrbe-map__modal .modal__body-text label textarea,.rrbe-map__modal .modal__body-text label input{margin:10px 0 20px;padding:5px;border:1px solid #919191}.rrbe-map__modal .modal__body-text label input[type="range"]{padding:0}.rrbe-map__modal .modal__body-icon{margin:10px 0 20px}.rrbe-map__modal .modal__body-icon button{background-color:#fff;border:0;padding:5px 8px;cursor:pointer;border-radius:3px}.rrbe-map__modal .modal__body-icon button.active{border:1px solid #343a40}.rrbe-map__modal .modal__body-save{cursor:pointer;display:block;border:0;background-color:#6c757d;padding:10px 15px;color:#fff}.rrbe-map__modal .modal__body-save:hover{background-color:#343a40}.leaflet-marker-icon.leaflet-div-icon:not(.leaflet-editing-icon){background:transparent;border:none}
